// pages/index.tsx
import { NextPage } from 'next';
import Head from 'next/head';

const Home: NextPage = () => {
  return (
    <>
      <Head>
        <title>GOtip - Red social para emprendedores</title>
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css" />
      </Head>

      <div className="min-h-screen bg-gray-50 text-gray-800">
        {/* Header */}
        <header className="bg-white border-b border-gray-200 sticky top-0 z-50 py-2">
          <div className="max-w-6xl mx-auto px-4 flex justify-between items-center">
            {/* Logo */}
            <div className="text-2xl font-extrabold flex items-center">
              <span className="text-blue-600">GO</span>
              <span className="text-orange-500">tip</span>
            </div>

            {/* Search Bar */}
            <div className="flex-grow mx-4">
              <input
                type="text"
                placeholder="Buscar emprendimientos, personas o ideas..."
                className="w-full px-4 py-2 rounded-full border border-gray-300 bg-gray-100 focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>

            {/* Navigation Icons */}
            <div className="flex space-x-4">
              <NavIcon icon="fas fa-home" label="Inicio" />
              <NavIcon icon="fas fa-network-wired" label="Mi red" />
              <NavIcon icon="fas fa-lightbulb" label="Ideas" />
              <NavIcon icon="fas fa-envelope" label="Mensajes" />
              <NavIcon icon="fas fa-bell" label="Notificaciones" />
            </div>

            {/* User Profile */}
            <div className="flex items-center space-x-2 px-3 py-1 rounded-lg hover:bg-blue-50 cursor-pointer">
              <img
                src="https://randomuser.me/api/portraits/men/32.jpg"
                alt="Perfil"
                className="w-8 h-8 rounded-full object-cover"
              />
              <span className="text-sm font-medium">Mi negocio <i className="fas fa-caret-down ml-1"></i></span>
            </div>
          </div>
        </header>

        {/* Main Content */}
        <main className="max-w-6xl mx-auto px-4 py-4 flex flex-col md:flex-row gap-4">
          {/* Left Sidebar */}
          <aside className="w-full md:w-1/4 bg-white rounded-xl border border-gray-200 shadow-sm p-4 h-fit">
            <ul className="space-y-1">
              <SidebarItem 
                icon="fas fa-lightbulb" 
                label="Ideas para emprender" 
                active 
                iconColor="text-green-500"
                bgColor="bg-green-50"
                borderColor="border-l-green-500"
              />
              <SidebarItem icon="fas fa-user" label="Mi perfil" />
              <SidebarItem icon="fas fa-store" label="Mi negocio" />
              <SidebarItem icon="fas fa-users" label="Contactos" />
              <SidebarItem icon="fas fa-calendar-alt" label="Eventos" />
              <SidebarItem icon="fas fa-chart-line" label="Estadísticas" />
              <SidebarItem icon="fas fa-wallet" label="Financiamiento" />
              <SidebarItem icon="fas fa-graduation-cap" label="Cursos" />
              <SidebarItem icon="fas fa-hands-helping" label="Mentorías" />
            </ul>
          </aside>

          {/* Center Content */}
          <div className="flex-1 max-w-2xl">
            {/* Create Post */}
            <div className="bg-white rounded-xl border border-gray-200 shadow-sm p-4 mb-4">
              <div className="flex items-center gap-2 mb-3">
                <img
                  src="https://randomuser.me/api/portraits/men/32.jpg"
                  alt="Usuario"
                  className="w-10 h-10 rounded-full object-cover"
                />
                <input
                  type="text"
                  placeholder="Comparte una idea, oferta o logro de tu negocio..."
                  className="flex-grow px-4 py-2 rounded-full border border-gray-300 bg-gray-100 focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div className="flex justify-between border-t border-gray-100 pt-3">
                <PostOption icon="fas fa-video" label="Video" iconColor="text-purple-500" />
                <PostOption icon="fas fa-images" label="Foto" iconColor="text-green-500" />
                <PostOption icon="fas fa-tag" label="Oferta" iconColor="text-orange-500" />
                <PostOption icon="fas fa-poll" label="Encuesta" iconColor="text-blue-500" />
              </div>
            </div>

            {/* Post 1 */}
            <Post 
              user={{
                name: "EcoTienda Verde",
                role: "NEGOCIO",
                avatar: "https://randomuser.me/api/portraits/women/65.jpg",
                info: "Productos ecológicos · CDMX · 2h",
              }}
              content={{
                text: "¡Lanzamos nuestra nueva línea de productos zero waste! 🌱 Desde cepillos de bambú hasta envolturas de cera de abeja. ¡30% de descuento esta semana para nuestros seguidores en StartUpConnect!",
                tags: "#Oferta #Ecológico #EmprendimientoMexicano",
                image: "https://source.unsplash.com/random/600x300/?eco,products",
              }}
              stats={{
                likes: 128,
                comments: 24,
                shares: 15,
              }}
              borderColor="border-l-orange-500"
              badge={{
                text: "NEGOCIO",
                color: "bg-purple-500",
              }}
              actionButton={{
                text: "Seguir",
                color: "bg-blue-600 hover:bg-blue-700",
              }}
            />

            {/* Post 2 */}
            <Post 
              user={{
                name: "Carlos Mendoza",
                role: "EMPRENDEDOR",
                avatar: "https://randomuser.me/api/portraits/men/22.jpg",
                info: "Fundador de Innovatech · 5h",
              }}
              content={{
                text: "🚀 Estoy buscando un cofundador con experiencia en desarrollo web para un proyecto de marketplace enfocado en artesanías mexicanas. Tengo el capital inicial y el plan de negocios, necesito a alguien apasionado por la tecnología y la cultura mexicana.",
                tags: "#BuscoSocio #Startup #Tecnología",
              }}
              stats={{
                likes: 45,
                comments: 18,
                shares: 7,
              }}
              borderColor="border-l-blue-500"
              badge={{
                text: "EMPRENDEDOR",
                color: "bg-green-500",
              }}
              customActions={[
                { icon: "fas fa-handshake", label: "Colaborar" }
              ]}
            />

            {/* Post 3 */}
            <Post 
              user={{
                name: "María González",
                role: "MENTORA",
                avatar: "https://randomuser.me/api/portraits/women/44.jpg",
                info: "Directora en Founder Institute · 8h",
              }}
              content={{
                text: "📣 ¡Gran evento para emprendedores este viernes! 'Financiamiento para Startups: De bootstrapping a VC'. Tendremos a inversionistas de Silicon Valley compartiendo sus mejores consejos.",
                additionalText: "📍 Modalidad híbrida (Zoom y WeWork Reforma)\n🕒 Viernes 15 de julio, 6pm\n💰 Entrada gratuita para miembros de StartUpConnect",
                image: "https://source.unsplash.com/random/600x300/?meeting,startup",
              }}
              stats={{
                attendees: 78,
                comments: 32,
              }}
              borderColor="border-l-green-500"
              badge={{
                text: "MENTORA",
                color: "bg-green-500",
              }}
              actionButton={{
                text: "Asistiré",
                color: "bg-green-500 hover:bg-green-600",
              }}
            />
          </div>

          {/* Right Sidebar */}
          <aside className="w-full md:w-1/4 bg-white rounded-xl border border-gray-200 shadow-sm p-4 h-fit">
            <h3 className="text-lg font-semibold mb-3 flex items-center gap-2 text-orange-500">
              <i className="fas fa-bolt"></i> Oportunidades
            </h3>
            
            <SponsoredItem 
              image="https://source.unsplash.com/random/100x70/?workshop"
              title="Taller: Marketing Digital para PYMES"
              description="¡Aprende a vender más! Cupo limitado"
            />
            
            <SponsoredItem 
              image="https://source.unsplash.com/random/100x70/?investors"
              title="Ronda de inversión para startups"
              description="Postula tu proyecto antes del 30/07"
            />
            
            <h3 className="text-lg font-semibold mt-6 mb-3 flex items-center gap-2">
              <i className="fas fa-users"></i> Emprendedores cerca de ti
            </h3>
            
            <ContactItem 
              image="https://randomuser.me/api/portraits/women/33.jpg"
              name="Laura Jiménez"
              description="Moda sostenible"
              online
            />
            
            <ContactItem 
              image="https://randomuser.me/api/portraits/men/45.jpg"
              name="Pedro Ramírez"
              description="Tech"
              online
            />
            
            <ContactItem 
              image="https://randomuser.me/api/portraits/women/28.jpg"
              name="Ana Sánchez"
              description="Gastronomía"
              online
            />
            
            <h3 className="text-lg font-semibold mt-6 mb-3 flex items-center gap-2">
              <i className="fas fa-hashtag"></i> Tendencias
            </h3>
            
            <TrendingItem 
              tag="#Fintech"
              posts="1,245 publicaciones"
            />
            
            <TrendingItem 
              tag="#Sustentabilidad"
              posts="892 publicaciones"
            />
          </aside>
        </main>

        {/* Footer */}
        <footer className="bg-white border-t border-gray-200 py-4 text-center text-sm text-gray-500">
          <p>© 2025 GOtip · <a href="#" className="text-blue-600 hover:underline">Privacidad</a> · <a href="#" className="text-blue-600 hover:underline">Términos</a> · <a href="#" className="text-blue-600 hover:underline">Opciones de anuncios</a></p>
        </footer>
      </div>
    </>
  );
};

// Componentes reutilizables
type NavIconProps = {
  icon: string;
  label: string;
};

const NavIcon: React.FC<NavIconProps> = ({ icon, label }) => (
  <a href="#" className="flex flex-col items-center px-2 py-1 rounded-lg hover:bg-blue-50">
    <i className={`${icon} text-blue-600 text-xl mb-1`}></i>
    <span className="text-xs">{label}</span>
  </a>
);

type SidebarItemProps = {
  icon: string;
  label: string;
  active?: boolean;
  iconColor?: string;
  bgColor?: string;
  borderColor?: string;
};

const SidebarItem: React.FC<SidebarItemProps> = ({ 
  icon, 
  label, 
  active = false, 
  iconColor = "text-blue-500",
  bgColor = "hover:bg-blue-50",
  borderColor = ""
}) => (
  <li className={`flex items-center gap-2 p-2 rounded-lg cursor-pointer ${active ? `bg-green-50 border-l-4 ${borderColor}` : bgColor}`}>
    <i className={`${icon} ${iconColor} text-lg`}></i>
    <span className="text-sm font-medium">{label}</span>
  </li>
);

type PostOptionProps = {
  icon: string;
  label: string;
  iconColor?: string;
};

const PostOption: React.FC<PostOptionProps> = ({ icon, label, iconColor = "text-blue-500" }) => (
  <button className={`flex items-center gap-1 px-3 py-1 rounded-lg bg-blue-50 hover:bg-blue-100 font-medium`}>
    <i className={`${icon} ${iconColor}`}></i>
    <span className="text-sm">{label}</span>
  </button>
);

type PostProps = {
  user: {
    name: string;
    role: string;
    avatar: string;
    info: string;
  };
  content: {
    text: string;
    tags?: string;
    additionalText?: string;
    image?: string;
  };
  stats: {
    likes?: number;
    comments?: number;
    shares?: number;
    attendees?: number;
  };
  borderColor: string;
  badge: {
    text: string;
    color: string;
  };
  actionButton?: {
    text: string;
    color: string;
  };
  customActions?: Array<{
    icon: string;
    label: string;
  }>;
};

const Post: React.FC<PostProps> = ({ 
  user, 
  content, 
  stats, 
  borderColor, 
  badge, 
  actionButton,
  customActions
}) => (
  <div className={`bg-white rounded-xl border border-gray-200 shadow-sm p-4 mb-4 border-l-4 ${borderColor}`}>
    {/* Post Header */}
    <div className="flex justify-between items-center mb-3">
      <div className="flex items-center gap-2">
        <img src={user.avatar} alt={user.name} className="w-10 h-10 rounded-full object-cover" />
        <div>
          <h4 className="text-sm font-semibold flex items-center">
            {user.name} <span className={`${badge.color} text-white text-xs px-2 py-0.5 rounded ml-2`}>{badge.text}</span>
          </h4>
          <p className="text-xs text-gray-500">{user.info}</p>
        </div>
      </div>
      {actionButton ? (
        <button className={`${actionButton.color} text-white text-xs px-3 py-1 rounded-md`}>
          {actionButton.text}
        </button>
      ) : (
        <i className="fas fa-ellipsis-h text-gray-400"></i>
      )}
    </div>

    {/* Post Content */}
    <div className="mb-3">
      <p className="text-sm mb-2">{content.text}</p>
      {content.tags && <p className="text-orange-500 font-semibold text-xs">{content.tags}</p>}
      {content.additionalText && (
        <pre className="whitespace-pre-wrap font-sans text-sm mt-2">{content.additionalText}</pre>
      )}
    </div>

    {/* Post Image */}
    {content.image && (
      <img src={content.image} alt="Post" className="w-full rounded-lg mb-3" />
    )}

    {/* Post Stats */}
    <div className="flex justify-between text-xs text-gray-500 py-2 border-b border-gray-100 mb-2">
      <div>
        {stats.likes !== undefined && (
          <span><i className="fas fa-thumbs-up mr-1"></i> {stats.likes}</span>
        )}
        {stats.attendees !== undefined && (
          <span><i className="fas fa-users mr-1"></i> {stats.attendees} asistentes</span>
        )}
      </div>
      <div>
        {stats.comments !== undefined && (
          <span>{stats.comments} comentarios</span>
        )}
        {stats.shares !== undefined && (
          <span> · {stats.shares} compartidos</span>
        )}
      </div>
    </div>

    {/* Post Actions */}
    <div className="flex justify-between">
      {customActions ? (
        customActions.map((action, index) => (
          <button key={index} className="flex items-center gap-1 px-3 py-1 rounded-lg hover:bg-blue-50 text-sm font-medium">
            <i className={`${action.icon} text-blue-500`}></i>
            <span>{action.label}</span>
          </button>
        ))
      ) : (
        <>
          <button className="flex items-center gap-1 px-3 py-1 rounded-lg hover:bg-blue-50 text-sm font-medium">
            <i className="far fa-thumbs-up text-blue-500"></i>
            <span>Me gusta</span>
          </button>
          <button className="flex items-center gap-1 px-3 py-1 rounded-lg hover:bg-blue-50 text-sm font-medium">
            <i className="far fa-comment text-blue-500"></i>
            <span>Comentar</span>
          </button>
          <button className="flex items-center gap-1 px-3 py-1 rounded-lg hover:bg-blue-50 text-sm font-medium">
            <i className="fas fa-share text-blue-500"></i>
            <span>Compartir</span>
          </button>
          <button className="flex items-center gap-1 px-3 py-1 rounded-lg hover:bg-blue-50 text-sm font-medium">
            <i className="fas fa-shopping-cart text-blue-500"></i>
            <span>Comprar</span>
          </button>
        </>
      )}
    </div>
  </div>
);

type SponsoredItemProps = {
  image?: string;
  title: string;
  description: string;
};

const SponsoredItem: React.FC<SponsoredItemProps> = ({ image, title, description }) => (
  <div className="flex items-center gap-2 p-2 rounded-lg hover:bg-blue-50 mb-3">
    {image && <img src={image} alt={title} className="w-20 h-14 rounded-lg object-cover" />}
    <div>
      <h5 className="text-sm font-medium">{title}</h5>
      <p className="text-xs text-gray-500">{description}</p>
    </div>
  </div>
);

type ContactItemProps = {
  image: string;
  name: string;
  description: string;
  online?: boolean;
};

const ContactItem: React.FC<ContactItemProps> = ({ image, name, description, online = false }) => (
  <div className="flex items-center gap-2 p-2 rounded-lg hover:bg-blue-50 cursor-pointer">
    <img src={image} alt={name} className="w-8 h-8 rounded-full object-cover" />
    <span className="text-sm flex-1">
      {name} <small className="text-gray-500">- {description}</small>
    </span>
    {online && <div className="w-2 h-2 rounded-full bg-green-500"></div>}
  </div>
);

type TrendingItemProps = {
  tag: string;
  posts: string;
};

const TrendingItem: React.FC<TrendingItemProps> = ({ tag, posts }) => (
  <div className="p-2 rounded-lg hover:bg-blue-50 mb-2">
    <h5 className="text-sm font-medium">{tag}</h5>
    <p className="text-xs text-gray-500">{posts}</p>
  </div>
);

export default Home;